<template>
	<div id="app">

		<audio controls="controls" :autoplay="autoplay" id="audio">
			<source src="../static/video/message.mp3"> 您的浏览器不支持 audio 元素。
		</audio>

		<div v-wechat-title="$route.meta.title"></div>
		<router-view></router-view>
	</div>
</template>
<script>
	export default {
		name: "App",
		data() {
			return {
				autoplay: false
				// transitionName: null
			};
		},
		watch: {
			// $route(to, from) {
			//   this.transitionName = "animated fadeInLeft routTo";
			// }
		},
		created() {
			// let token = localStorage.getItem("token");
			// 	if(token){
			// 		this.$router.push({
			// 					path: "/login"
			// 		});
			// 	}
		},
		methods: {

			// 初始化
			socket_init() {
				var user_id = localStorage.getItem("k2_userId");
				if(user_id) {
					this.socketApi.sendSock({
						"type": "init",
						"user_id": user_id
					}, this.getConfigResult);

					var k_msgcount = localStorage.getItem("k2_msgcount")
					if(k_msgcount * 1 > 0) {
						const data = {
							k2_msgcount: 1
						}
						this.$root.eventHub.$emit('eventName', data)
					}
				}
			},

			//
			// 获得长链接消息
			getConfigResult(res) {
				if(res.type == "push") {
					var audio = document.querySelector("audio");
					audio.play();
					localStorage.setItem("k2_msgcount", 1);
					// 传值给left
					const data = {
						k2_msgcount: 1
					}
					this.$root.eventHub.$emit('eventName', data)
				}
				console.log(res)
			},

			//检测浏览器放大比例
			detectZoom() {
				var ratio = 0,
					screen = window.screen,
					ua = navigator.userAgent.toLowerCase();

				if(window.devicePixelRatio !== undefined) {
					ratio = window.devicePixelRatio;
				} else if(~ua.indexOf('msie')) {
					if(screen.deviceXDPI && screen.logicalXDPI) {
						ratio = screen.deviceXDPI / screen.logicalXDPI;
					}
				} else if(window.outerWidth !== undefined && window.innerWidth !== undefined) {
					ratio = window.outerWidth / window.innerWidth;
				}

				if(ratio) {
					ratio = Math.round(ratio * 100);
				}
				return ratio;
			}

		},
		mounted() {

			//111
			//初始化
			this.socket_init();
			
			var size = this.detectZoom();
			
			localStorage.setItem("Clientsize" , size)

		}
	};
</script>

<style>
	#app {
		border-top: 1px solid transparent;
	}
	
	.main {
		overflow-y: auto;
		overflow-x: hidden;
	}
	
	#audio {
		opacity: 0;
		position: absolute;
		top: 0;
		left: 0;
	}
</style>


